import React, { useEffect, useState } from "react";
import styles from "./index.module.scss";
// import dropdownArrow from "../../../../../assets/icons/dropdownArrow.svg";
import dropdownArrow from "../../../../assets/icons/dropdownArrow.svg";
import UpArrow from "../../../../assets/icons/upArrow.svg";
import InputBox from "components/UI/common/InputBox/InputBox";
import FormErrorMessage from "components/UI/common/ErrorMessage";

const UnitNamingTable = ({ initialData, tabelHeader, tabelHeader2, tabelHeader3, errors, control, register, onToggleBulkUpload, isManual, activeTab }) => {

  // array to keep track of which dropdowns are open
  const [openDropdowns, setOpenDropdowns] = useState(
    Array(initialData.length).fill(false)
  );
  const [combinedData, setCombinedData] = useState([]);
  const [activeFloor, setActiveFloor] = useState(null);

  const toggleDropdown = (index, floorName) => {
    const updatedDropdowns = [...openDropdowns];
    updatedDropdowns[index] = !updatedDropdowns[index];
    setOpenDropdowns(updatedDropdowns);
    onToggleBulkUpload();

    if (floorName === activeFloor) setActiveFloor(null)
    else setActiveFloor(floorName);
  };

  // handleInputChange function to update the input values
  const handleInputChange = (e, rowIndex, columnName) => {
    // const updatedData = [...tableData];
    // updatedData[rowIndex][columnName] = e.target.value;
    // setTableData(updatedData);
  };

  useEffect(() => {
    if (initialData?.length) {
      const uniqueFloorNames = [...new Set(initialData.flatMap(a => a.blockName === activeTab ? [a.floorName] : []))];
      if (uniqueFloorNames?.length) {
        const tempArr = uniqueFloorNames.map(floorName => {
          let obj = { floorName };
          const flatOnFloor = initialData.filter(data => data.floorName === floorName && data.blockName === activeTab);
          if (flatOnFloor?.length) {
            obj.combinedSequence = flatOnFloor[0].flatSequence + " - " + flatOnFloor[flatOnFloor.length - 1].flatSequence
            obj.flatCounts = flatOnFloor.length;
          }
          return obj;
        });

        setCombinedData(tempArr)
      }
    }
  }, [initialData, activeTab, isManual]);

  return (
    <>
      <div className={styles.tableContainer}>
        <table className={styles.table}>
          <thead>
            <tr>
              <th>{`Floor`}</th>
              <th className={styles.propertyName}>
                <div className={styles.columnContainer}>{`Number of Flats`}</div>
              </th>
              {isManual && <th>{`Flat Number`}</th>}
              <th>{`Flats Sequence`}</th>
            </tr>
          </thead>
          <tbody>
            {combinedData.length && combinedData.map((row, index) => {
              return (<React.Fragment key={index}>
                <tr>
                  <td className="px-4">
                    <span
                      className={styles.dropdownArrow}
                      onClick={() => toggleDropdown(index, row.floorName)}
                    >
                      {activeFloor === row.floorName ? (
                        <img
                          src={UpArrow}
                          alt="Dropdown Arrow"
                          style={{ width: "10px", height: "10px" }}
                        />
                      ) : (
                        <img
                          src={dropdownArrow}
                          alt="Dropdown Arrow"
                        /> /* Unicode for drop-down arrow */
                      )}
                    </span>
                    <span>
                      {row.floorName || 0}
                    </span>
                  </td>
                  <td className="px-4">
                    <span>
                      {row?.flatCounts}
                      {isManual && "  Flats"}
                    </span>
                  </td>
                  {isManual && <td className="px-4">
                    <span className="d-inline-block">
                      {" "}
                    </span></td>}
                    <td className={!isManual && styles.color} style={{ background: isManual ? "#FFF" : "#F3F5E7" }}>
                    <span>
                      {isManual ? "" : row.combinedSequence || ""}
                    </span>
                  </td>
                </tr>
                {activeFloor === row.floorName && initialData.length > 0 && (
                  <>
                    {!isManual && <tr className={styles.dropdownTable}>
                      <td className="px-4">Flat Count</td>
                      <td>Flat Number</td>
                      <td>Flat Sequence</td>
                    </tr>}

                    {initialData.map((input, i) => (
                      <tr key={i} className={`${styles.dropdownTable} ${(input?.floorName === activeFloor && input?.blockName === activeTab) ? "" : "d-none"}`}>
                        {isManual && <td>
                        </td>}
                        <td className="px-4">
                          <InputBox
                            type="text"
                            ratefiled
                            name={`flatDetails.${i}.flatcount`}
                            readOnly
                            register={register(`flatDetails.${i}.flatcount`)}
                          />
                        </td>
                        <td>
                          <InputBox
                            type="text"
                            ratefiled
                            name={`flatDetails.${i}.flatnumber`}
                            placeholder="Flat Number"
                            readOnly={!isManual}
                            register={register(`flatDetails.${i}.flatnumber`, {
                              required: true,
                              pattern: /^[0-9\b]+$/,
                              validate: {
                                max100: (value) => {
                                  if (parseInt(value) > 100) {
                                    return "Maximum value is 100";
                                  }
                                  return true;
                                },
                              },
                            })}
                          />
                          <FormErrorMessage
                            className={styles.error}
                            error={
                              errors?.flatDetails?.[i]?.flatnumber
                            }
                            messages={{
                              required: "Flat Number is required",
                              pattern: "Flat Number is invalid",
                              max100: "Maximum Flat Number is 100",
                            }}
                          />
                        </td>
                        <td className={isManual && styles.color}>
                          <InputBox
                            type="text"
                            ratefiled
                            placeholder="Flat Sequence"
                            name={`flatDetails.${i}.flatSequence`}
                            readOnly={!isManual}
                            register={register(`flatDetails.${i}.flatSequence`, {
                              required: true,
                              pattern: /^[0-9\b]+$/,
                              validate: {
                                max100: (value) => {
                                  if (parseInt(value) > 100) {
                                    return "Maximum value is 100";
                                  }
                                  return true;
                                },
                              },
                            })}
                          />
                          <FormErrorMessage
                            className={styles.error}
                            error={
                              errors?.flatDetails?.[i]?.flatSequence
                            }
                            messages={{
                              required: "Flat Sequence is required",
                              pattern: "Flat Sequence is invalid",
                              max100: "Maximum Flat Sequence is 100",
                            }}
                          />
                        </td>
                      </tr>
                    ))}
                  </>
                )}
              </React.Fragment>)
            })}
          </tbody>
        </table>
      </div>
    </>
  );
};
export default UnitNamingTable;
